.history-container{
    display: inline-flex;
    text-align: right;
    list-style: none;
    align-self: flex-start;
    width: 100%;
    margin-bottom: 5px;

    
}
.date-container{
    display: inline-flex;
    
   /* height: 75px;
    width: 100px;*/
}
.date-text{
    margin: 20px;
}
.details-container{
    
    margin-left: 10px;
    display: inline-flex;
    border-left: 2px solid rgb(255, 30, 0);
    margin-left: 5px;
    height: 100%;
    width: 100%;
    text-align: left;
    
}
.details-content{
    
    display: flex;
    height: inherit;
    width: inherit;
    flex-direction: row;
   
}
.details-text-container{
  
    display: inline-flex;
    flex-direction: row;
    height: inherit;
    width: 60%;
    
}
.details-text-container-no-image{
   
    display: inline-flex;
    flex-direction: row;
    height: inherit;
  
    width: 100%;
}
.details-text-wrapper{
    
    display: flex;
    flex-direction: column;
    height: inherit;
    width: 100%;
}
.details-text,.details-text-charge,.details-text-title,.details-text-desc,.details-text-hyperlink{
    padding: 0;
    margin: 0;
}
.details-text-title{
    margin-left: 5px;
    margin-bottom: 5px;
}
.details-text-charge{
    margin-left: 10px;
    font-size: 12px;
    margin-bottom: 5px;
}
.details-text-desc{
    margin-left: 5px;
    font-size: 12px;
    
}
.details-text-hyperlink{
    top: 25px;
    font-size: 15px;
    position: relative;
        
}
.title-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
    border-top: 2px solid grey;
  
}

.charge-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
    margin-bottom: 5px;
    
}

.read-more-segment{
    display: flex;
    flex-direction: column;
    height: 15%;
    width: 100%;
    border-top: 3px solid rgba(246, 255, 0, 0.805);
}

.hyperlink{    
        text-decoration: none;
        color:rgb(255, 72, 0);
        font-weight: bold;        
        margin-bottom: 10px;
        font-size: 14px;    
}
.hyperlink-not-ready{
    
    text-decoration: none;
    color:rgb(255, 72, 0);
    font-weight: bold;        
    margin: 0;
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 14px;

}
.hyperlink > .hyperlink:last-child{
    border-bottom: 6px solid grey;
}

.image-container{
 
    display: inline-flex;    
    align-self: center;
    height: inherit;
    width: 40%;
    border-radius: 30px;
    border: 1px solid yellow;

}

.button {
    transition-duration: 0.4s;
    background-color: #f44336;
    color: rgb(255, 255, 255);
    font-size: 15px;
    border: 2px solid #020202; /* Green */
    border-radius: 8px;    
}
.button:hover {
    background-color: #661e19; /* Green */
    color: white;
}

.button-image {
    background: transparent;
    border-radius: 28px;
    transition-duration: 0.1s;  
    }
  .button-image:hover {
      border-radius: 28px;
      border: 1px rgb(255, 102, 0) solid;
      transition-duration: 0.05s;  
      }
    
.center {
    display: block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    
  }
.center-button {
    display: block;
    width:100%;
    height: 100%;
    border-radius: 30px;
    object-fit: scale-down;
    
      }

@media (min-width: 20rem){
    .details-content{               
        flex-direction: column-reverse;      
    }
    .details-text-container{
        flex-direction: column;
        align-items: flex-end;
        width: 100%;
        height: 270px;
    }
    .image-container{       
        width: 90%;
    }
    .read-more-segment{
        border: 0;
        min-height: 10%;
        padding: 5px;
    }
    }
@media (min-width: 40rem){
    .details-content{               
        flex-direction: row;      
    }
    .details-text-container{
        flex-direction: row;    
        width: 100%;
    }
    .image-container{       
        width: 50%;
    }
    .read-more-segment{
        border-top: 3px solid rgba(246, 255, 0, 0.805);
    }
    }

@media (min-width: 80rem){
    .history-container{
        min-height: 250px;
    }
    .title-segment{
        height: 15%;
        margin-bottom: 5px;
    }
    .desc-segment{
        min-height: 60%;
    }
    .read-more-segment{
        height: 20%;
        
    }
    .charge-segment{
        margin-bottom: 0px;
    }
    .details-text-container{
        width: 70%;
        height: 350px;
    }
    .date-container{
        height: 150px;        
    }
    .details-text-charge{
        font-size: 15px;
    }
    .details-text-desc{
        font-size: 20.5px;
    }
    }
    