.details-text,.details-text-charge,.details-text-title,.details-text-desc,.details-text-hyperlink{
    padding: 0;
    margin: 0;
}
.details-text-title{
    margin-left: 8px;
    margin-bottom: 5px;
}
.details-text-charge{
    margin-left: 10px;
    font-size: 12px;
    margin-bottom: 5px;
}
.details-text-desc{
    margin-left: 5px;
    font-size: 12px;
    
}

.title-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
   
  
}

.charge-segment{
    display: flex;
    flex-direction: column;
    height: 10%;
    width: 100%;
    margin-bottom: 5px;
    
}
.desc-segment{
    display: flex;
    flex-direction: column;
    min-height: 30%;
    width: 100%;
    margin-bottom: 5px;
   
}
@media (min-width: 20rem){
    .title-segment{
        
        height: 15%; 
        border: 0;
        border-bottom: 1px solid yellow;
    }
    .desc-segment{
        padding: 5px;
        margin-bottom: 0px;
        
    }  
    
}
@media (min-width: 40rem){
    .title-segment{
        height: 15%;
        margin-bottom: 5px;
        flex-direction: row;
    }
}

@media (min-width: 80rem){
    .history-container{
        padding: auto;        
    }
    .title-segment{
        height: 15%;
        margin-bottom: 5px;
        flex-direction: row;
    }
    .desc-segment{
        min-height: 60%;
    }
    .read-more-segment{
        height: 20%;
    }
    .charge-segment{
        margin-bottom: 0px;
    }
    .details-text-container{
        width: 80%;
    }
    .date-container{
        /*
        height: 150px;        */
    }
    .details-text-charge{
        font-size: 15px;
    }
    .details-text-desc{
        font-size: 20.5px;
    }
    }
    